
import { defineComponent } from "vue";
import {
  toFavoriteIcon,
  fromFavoriteIcon,
  heartIcon
} from "@/components/icons";

export default defineComponent({
  name: "card-item",
  components: { toFavoriteIcon, fromFavoriteIcon, heartIcon },
  props: {
    src: {
      type: String
    },
    isFavorite: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    triggerFavorite() {
      this.$emit("onTriggerFavorite", {
        src: this.src,
        isFavorite: this.isFavorite
      });
    }
  }
});
